      ];

      /**
       * üì• ËºâÂÖ•‰∏ñÁïåÂú∞ÂúñÊï∏Êìö
       */
      const loadWorldData = async () => {
        try {
          // ‰ΩøÁî®Êú¨Âú∞ÁöÑ GeoJSON Ê™îÊ°à
          console.log('[MapTab] ÈñãÂßãËºâÂÖ• GeoJSON Êï∏Êìö...');
          const response = await fetch(
            `${process.env.BASE_URL}data/ne_110m_admin_0_countries.geojson`
          );

          if (!response.ok) {
            throw new Error(`HTTP ÈåØË™§! ÁãÄÊÖã: ${response.status}`);
          }

          const data = await response.json();
          worldData.value = data;
          console.log('[MapTab] ‰∏ñÁïåÂú∞ÂúñÊï∏ÊìöËºâÂÖ•ÊàêÂäüÔºåÁâπÂæµÊï∏Èáè:', data.features?.length);
          return true;
        } catch (error) {
          console.error('[MapTab] ‰∏ñÁïåÂú∞ÂúñÊï∏ÊìöËºâÂÖ•Â§±Êïó:', error);
          return false;
        }
      };

      /**
       * üèóÔ∏è ÂâµÂª∫Âú∞ÂúñÂØ¶‰æã
       * ÂàùÂßãÂåñ D3 Âú∞Âúñ‰∏¶Ë®≠ÂÆöÂü∫Êú¨ÈÖçÁΩÆ
       */
      const createMap = () => {
        if (!mapContainer.value) return false;

        const rect = mapContainer.value.getBoundingClientRect();
        if (rect.width === 0 || rect.height === 0) {
          console.warn('[MapTab] ÂÆπÂô®Â∞∫ÂØ∏ÁÇ∫Èõ∂ÔºåÂª∂ÈÅ≤ÂàùÂßãÂåñ');
          return false;
        }

        try {
          const width = rect.width;
          const height = rect.height;

          // ÂâµÂª∫ SVG ÂÖÉÁ¥†
          svg = d3
            .select(mapContainer.value)
            .append('svg')
            .attr('width', width)
            .attr('height', height)
            .style('background', '#f0f0f0');

          svgElement.value = svg.node();

          d3.select(mapContainer.value).style('position', 'relative');

          if (!ringTooltip) {
            ringTooltip = d3
              .select(mapContainer.value)
              .append('div')
              .attr('class', 'ring-tooltip')
              .style('position', 'absolute')
              .style('pointer-events', 'none')
              .style('background', 'rgba(15, 23, 42, 0.85)')
              .style('color', '#f8fafc')
              .style('padding', '0.35rem 0.55rem')
              .style('border-radius', '0.5rem')
              .style('font-size', '0.75rem')
              .style('line-height', '1.2')
              .style('white-space', 'nowrap')
              .style('opacity', 0);
          }

          // ÂâµÂª∫ÊäïÂΩ± - ‰ΩøÁî®Êñπ‰ΩçÁ≠âË∑ùÊäïÂΩ± (Azimuthal Equidistant Projection)
          // È†êË®≠‰ª•Âè∞ÁÅ£Âú∞ÁêÜ‰∏≠ÂøÉÁÇ∫ÊäïÂΩ±‰∏≠ÂøÉ
          const scale = getScale(rect);

          projection = d3
            .geoAzimuthalEquidistant()
            .rotate([-120.982025, -23.973875]) // ‰ª•Âè∞ÁÅ£Âú∞ÁêÜ‰∏≠ÂøÉÁÇ∫‰∏≠ÂøÉ
            .scale(scale) // ‰ΩøÁî®Ë®àÁÆóÂæåÁöÑÁ∏ÆÊîæÊØî‰æã
            .translate([width / 2, height / 2])
            .clipAngle(180);

          // ÂâµÂª∫Ë∑ØÂæëÁîüÊàêÂô®
          path = d3.geoPath().projection(projection);

          // ÂâµÂª∫ÂÆπÂô®ÁµÑ
          g = svg.append('g');

          // Ë®≠ÁΩÆÁ∏ÆÊîæË°åÁÇ∫ÔºàÁ¶ÅÁî®ÊâÄÊúâ‰∫íÂãïÔºâ
          zoom = d3
            .zoom()
            .scaleExtent([1, 1]) // Á¶ÅÁî®Á∏ÆÊîæ
            .on('zoom', null); // Á¶ÅÁî®Á∏ÆÊîæ‰∫ã‰ª∂

          svg.call(zoom).on('wheel.zoom', null).on('dblclick.zoom', null);

          isMapReady.value = true;

          // Â∞áÂú∞ÂúñÂØ¶‰æãÂíåÊñπÊ≥ï‰∏ÄËµ∑ÂÇ≥ÈÅû
          const mapInterface = {
            svg,
            projection,
            path,
            navigateToLocation: (center) => navigateToLocation(center),
          };

          emit('map-ready', mapInterface);

          console.log('[MapTab] D3 Âú∞ÂúñÂâµÂª∫ÊàêÂäü');
          return true;
        } catch (error) {
          console.error('[MapTab] D3 Âú∞ÂúñÂâµÂª∫Â§±Êïó:', error);
          return false;
        }
      };

      // Ë∑ùÈõ¢ÂúìÂúàËàáÂüéÂ∏ÇÊ®ôË®ò
      let ringsGroup = null;
      let cityGroup = null;
      let tooltipGroup = null;
      let ringTooltip = null;

      /**
       * üîµ Áπ™Ë£Ω‰ª•ÊäïÂΩ±‰∏≠ÂøÉÁÇ∫ÂúìÂøÉÁöÑÂêåÂøÉË∑ùÈõ¢Âúì
       * ‰ΩøÁî®ÊåáÂÆöÂçäÂæëÔºàÂÖ¨ÈáåÔºâÁπ™Ë£ΩËôõÁ∑öÂúìÂúà
       * Âú∞ÁêÉÈÇäÁïåÔºà180¬∞Ôºâ‰ªç‰øùÁïôÂØ¶Á∑öÂúìÂúà
       */
      const drawDistanceRings = () => {
        if (!svg || !projection || !mapContainer.value) return;

        if (ringTooltip) {
          ringTooltip.style('opacity', 0);
        }

        const rect = mapContainer.value.getBoundingClientRect();
        const cx = rect.width / 2;
        const cy = rect.height / 2;
        const scale = projection.scale();

        // Âú∞ÁêÉÂçäÂæëÔºàÂÖ¨ÈáåÔºâ
        const earthRadiusMeters = 6371000;
        const selectedConfig = ringConfigurations[ringMode.value] || ringConfigurations.distance;

        const rings = selectedConfig.radiiKm.map((distanceKm, idx) => {
          const adjustedDistanceKm = ringMode.value === 'radius' ? distanceKm / 10 : distanceKm;
          const distanceMeters = adjustedDistanceKm * 1000;
          const radiusPx = scale * (distanceMeters / earthRadiusMeters);
          const label = selectedConfig.names?.[idx] || `Ë°åÊòü ${idx + 1}`;
          const formattedValue = selectedConfig.formatter
            ? selectedConfig.formatter.format(distanceKm)
            : `${distanceKm}`;
          return {
            index: idx,
            radiusPx,
            type: ringMode.value,
            label,
            formattedValue,
            unit: selectedConfig.unit || 'km',
            originalValue: distanceKm,
          };
        });

        // Âä†ÂÖ•Âú∞ÁêÉÈÇäÁïåÂúìÔºà180¬∞ = œÄ * RÔºåÂú®Êñπ‰ΩçÁ≠âË∑ùÊäïÂΩ±‰∏≠Â∞çÊáâÂà∞ scale * œÄÔºâ
        const earthBoundaryRadiusPx = scale * Math.PI;
        rings.push({ index: 999, radiusPx: earthBoundaryRadiusPx, type: 'boundary' });

        if (!ringsGroup) {
          ringsGroup = svg
            .append('g')
            .attr('class', 'distance-rings')
            .style('pointer-events', 'auto');
        }

        const selection = ringsGroup.selectAll('circle.ring').data(rings, (d) => d.index);

        const selectionEnter = selection
          .enter()
          .append('circle')
          .attr('class', 'ring')
          .attr('fill', 'none');

        selectionEnter
          .merge(selection)
          .attr('cx', cx)
          .attr('cy', cy)
          .attr('r', (d) => d.radiusPx)
          .attr('stroke', (d) => {
            if (d.type === 'boundary') return '#666666';
            return ringMode.value === 'radius' ? '#8be9fd' : '#cccccc';
          })
          .attr('stroke-width', (d) => (d.type === 'boundary' ? 2 : 1))
          .attr('stroke-dasharray', 'none')
          .attr('pointer-events', (d) => (d.type === 'boundary' ? 'none' : 'visibleStroke'))
          .on('mouseenter', function (event, d) {
            if (!ringTooltip || d.type === 'boundary') return;

            const valueText =
              ringMode.value === 'radius'
                ? `${d.formattedValue} ${d.unit} (1/10 Áπ™Ë£Ω)`
                : `${d.formattedValue} ${d.unit}`;

            ringTooltip
              .style('opacity', 1)
              .html(`<strong>${d.label}</strong><div>${valueText}</div>`);

            const [x, y] = d3.pointer(event, mapContainer.value);
            ringTooltip.style('left', `${x + 12}px`).style('top', `${y - 12}px`);

            d3.select(this).attr('stroke-width', d.type === 'boundary' ? 2 : 2);
          })
          .on('mousemove', function (event, d) {
            if (!ringTooltip || d.type === 'boundary') return;

            const [x, y] = d3.pointer(event, mapContainer.value);
            ringTooltip.style('left', `${x + 12}px`).style('top', `${y - 12}px`);
          })
          .on('mouseleave', function (event, d) {
            if (ringTooltip) {
              ringTooltip.style('opacity', 0);
            }

            d3.select(this).attr('stroke-width', d.type === 'boundary' ? 2 : 1);
          });

        selection.exit().remove();
      };

      /**
       * üìç Áπ™Ë£ΩÂüéÂ∏ÇÊ®ôË®ò
       */
      const drawCityMarkers = () => {
        if (!svg || !projection) return;

        if (!cityGroup) {
          cityGroup = svg.append('g').attr('class', 'city-markers');
        }

        if (!tooltipGroup) {
          tooltipGroup = svg
            .append('g')
            .attr('class', 'city-tooltips')
            .style('pointer-events', 'none');
        }

        const markers = cityGroup
          .selectAll('circle.city-marker')
          .data(cityLocations, (d) => d.name);
        const tooltipLabels = tooltipGroup
          .selectAll('text.city-tooltip')
          .data(cityLocations, (d) => d.name);

        markers
          .enter()
          .append('circle')
          .attr('class', 'city-marker')
          .attr('r', 3.5)
          .attr('fill', '#ffde59')
          .attr('stroke', '#0f172a')
          .attr('stroke-width', 1)
          .style('cursor', 'pointer')
          .on('mouseenter', function (event, d) {
            tooltipGroup
              .selectAll('text.city-tooltip')
              .filter((t) => t.name === d.name)
              .attr('visibility', 'visible');

            d3.select(this).attr('r', 5);
          })
          .on('mouseleave', function (event, d) {
            tooltipGroup
              .selectAll('text.city-tooltip')
              .filter((t) => t.name === d.name)
              .attr('visibility', 'hidden');

            d3.select(this).attr('r', 3.5);
          })
          .merge(markers)
          .attr('cx', (d) => {
            const projected = projection(d.coordinates);
            return projected ? projected[0] : 0;
          })
          .attr('cy', (d) => {
            const projected = projection(d.coordinates);
            return projected ? projected[1] : 0;
          });

        markers.exit().remove();

        tooltipLabels
          .enter()
          .append('text')
          .attr('class', 'city-tooltip')
          .attr('visibility', 'hidden')
          .attr('text-anchor', 'middle')
          .attr('dy', -8)
          .attr('fill', '#ffffff')
          .attr('font-size', 12)
          .attr('font-weight', '600')
          .attr('stroke', '#0f172a')
          .attr('stroke-width', 0.5)
          .attr('paint-order', 'stroke')
          .text((d) => d.label)
          .merge(tooltipLabels)
          .attr('x', (d) => {
            const projected = projection(d.coordinates);
            return projected ? projected[0] : 0;
          })
          .attr('y', (d) => {
            const projected = projection(d.coordinates);
            return projected ? projected[1] : 0;
          });

        tooltipLabels.exit().remove();
      };

      /**
       * üé® Áπ™Ë£Ω‰∏ñÁïåÂú∞Âúñ
       * È°ØÁ§∫ÊâÄÊúâÂúãÂÆ∂Ôºå‰∏¶‰ª•‰∏çÂêåÈ°èËâ≤Ê®ôË®òÂÆ∂ÈÑâËàáÂ∑≤ÈÄ†Ë®™ÂúãÂÆ∂
       */
      const drawWorldMap = async () => {
        if (!g || !worldData.value) {
          console.error('[MapTab] ÁÑ°Ê≥ïÁπ™Ë£ΩÂú∞Âúñ: g=', !!g, 'worldData=', !!worldData.value);
          return;
        }

        try {
          const features = worldData.value.features || [];

          console.log('[MapTab] ÈñãÂßãÁπ™Ë£Ω‰∏ñÁïåÂú∞ÂúñÔºåÂúãÂÆ∂Êï∏Èáè:', features.length);

          const countrySelection = g.selectAll('path.country').data(features, (feature) => {
            return (
              feature.id ||
              feature.properties?.iso_a3 ||
              feature.properties?.ADM0_A3 ||
              feature.properties?.name ||
              feature.properties?.NAME
            );
          });

          countrySelection
            .enter()
            .append('path')
            .attr('class', 'country')
            .attr('fill', '#192133')
            .attr('stroke', '#cbd5f5')
            .attr('stroke-width', 0.5)
            .merge(countrySelection)
            .attr('d', path)
            .attr('fill', '#192133')
            .attr('stroke', '#cbd5f5')
            .attr('stroke-width', 0.5)
            .attr('opacity', 0.95);

          countrySelection.exit().remove();

          // Áπ™Ë£ΩË∑ùÈõ¢ÂúìÂúà
          drawDistanceRings();

          // Áπ™Ë£ΩÂüéÂ∏ÇÊ®ôË®ò
          drawCityMarkers();
        } catch (error) {
          console.error('[MapTab] ‰∏ñÁïåÂú∞ÂúñÁπ™Ë£ΩÂ§±Êïó:', error);
        }
      };

      /**
       * üåç Â∞éËà™Âà∞ÊåáÂÆö‰ΩçÁΩÆ
       * ‰ΩøÁî®Êñπ‰ΩçÁ≠âË∑ùÊäïÂΩ±ÔºåÂ∞áÈÅ∏ÂÆöÁöÑÂúãÂÆ∂Ë®≠ÁÇ∫Âú∞Âúñ‰∏≠ÂøÉ
       * Âú∞ÁêÉÂ§ßÂ∞è‰øùÊåÅ‰∏çËÆäÔºåÂè™ÊîπËÆäÊóãËΩâ‰∏≠ÂøÉ
       */
      const navigateToLocation = (center) => {
        if (!svg || !projection) return;

        const rect = mapContainer.value.getBoundingClientRect();
        const width = rect.width;
        const height = rect.height;

        // Êñπ‰ΩçÁ≠âË∑ùÊäïÂΩ±Ôºö‰ΩøÁî® rotate Â∞áÈÅ∏ÂÆö‰ΩçÁΩÆÊóãËΩâÂà∞‰∏≠ÂøÉ
        // rotate Êé•Âèó [lambda, phi, gamma]ÔºåÂÖ∂‰∏≠ lambda Âíå phi ÊòØÁ∂ìÁ∑ØÂ∫¶ÁöÑË≤†ÂÄº
        // Âú∞ÁêÉÂ§ßÂ∞è‰øùÊåÅÂõ∫ÂÆöÔºå‰∏çÈö®Â∞éËà™ÊîπËÆä
        const scale = getScale(rect);

        projection
          .rotate([-center[0], -center[1]])
          .translate([width / 2, height / 2])
          .scale(scale);

        // Êõ¥Êñ∞ÊâÄÊúâË∑ØÂæë
        g.selectAll('path.country').attr('d', path);

        // Êõ¥Êñ∞Ë∑ùÈõ¢ÂúìÂúà
        drawDistanceRings();

        // Êõ¥Êñ∞ÂüéÂ∏ÇÊ®ôË®ò
        drawCityMarkers();

        console.log('[MapTab] Âú∞ÂúñÂ∞éËà™ÂÆåÊàêÔºå‰∏≠ÂøÉ:', center);
      };

      const refreshProjection = () => {
        if (!projection || !g || !mapContainer.value) return;

        const rect = mapContainer.value.getBoundingClientRect();
        const scale = getScale(rect);

        if (ringTooltip) {
          ringTooltip.style('opacity', 0);
        }

        projection.translate([rect.width / 2, rect.height / 2]).scale(scale);

        g.selectAll('path.country').attr('d', path);
        drawDistanceRings();
        drawCityMarkers();
      };

      const changeRingMode = (mode) => {
        if (!ringConfigurations[mode]) return;
        ringMode.value = mode;
      };

      /**
       * üìè Âà∑Êñ∞Âú∞ÂúñÂ∞∫ÂØ∏
       * Áï∂ÂÆπÂô®Â§ßÂ∞èÊîπËÆäÊôÇÈáçÊñ∞Ë®àÁÆóÂú∞ÂúñÂ∞∫ÂØ∏
       */
      const invalidateSize = () => {
        if (!svg || !mapContainer.value) return;

        const rect = mapContainer.value.getBoundingClientRect();
        const width = rect.width;
        const height = rect.height;

        svg.attr('width', width).attr('height', height);

        const scale = getScale(rect);

        projection.translate([width / 2, height / 2]).scale(scale);

        // Êõ¥Êñ∞ÊâÄÊúâË∑ØÂæë
        g.selectAll('path.country').attr('d', path);

        // Êõ¥Êñ∞Ë∑ùÈõ¢ÂúìÂúà
        drawDistanceRings();

        // Êõ¥Êñ∞ÂüéÂ∏ÇÊ®ôË®ò
        drawCityMarkers();

        console.log('[MapTab] Âú∞ÂúñÂ∞∫ÂØ∏Êõ¥Êñ∞ÂÆåÊàê');
      };

      /**
       * üöÄ ÂàùÂßãÂåñÂú∞Âúñ
       * ÂâµÂª∫Âú∞Âúñ‰∏¶ËºâÂÖ•ÂàùÂßãÊï∏Êìö
       */
      const initMap = async () => {
        let attempts = 0;
        const maxAttempts = 20;

        // ÂÖàËºâÂÖ•‰∏ñÁïåÂú∞ÂúñÊï∏Êìö
        const loaded = await loadWorldData();
        if (!loaded) {
          console.error('[MapTab] ÁÑ°Ê≥ïËºâÂÖ•‰∏ñÁïåÂú∞ÂúñÊï∏Êìö');
          return;
        }

        const tryCreateMap = async () => {
          if (attempts >= maxAttempts) {
            console.error('[MapTab] Âú∞ÂúñÂàùÂßãÂåñÂ§±ÊïóÔºåÂ∑≤ÈÅîÂà∞ÊúÄÂ§ßÂòóË©¶Ê¨°Êï∏');
            return;
          }

          attempts++;
          console.log(`[MapTab] ÂòóË©¶ÂâµÂª∫Âú∞Âúñ (${attempts}/${maxAttempts})`);

          if (createMap()) {
            console.log('[MapTab] Âú∞ÂúñÂâµÂª∫ÊàêÂäüÔºåÈñãÂßãÁπ™Ë£Ω‰∏ñÁïåÂú∞Âúñ');
            await drawWorldMap();
          } else {
            console.log('[MapTab] Âú∞ÂúñÂâµÂª∫Â§±ÊïóÔºå100ms ÂæåÈáçË©¶');
            setTimeout(tryCreateMap, 100);
          }
        };

        tryCreateMap();
      };

      // üìè Ë®≠ÁΩÆ ResizeObserver Áõ£ËÅΩÂÆπÂô®Â§ßÂ∞èËÆäÂåñ
      let resizeObserver = null;
      let resizeTimeout = null;

      const setupResizeObserver = () => {
        if (!mapContainer.value || !window.ResizeObserver) return;

        resizeObserver = new ResizeObserver(() => {
          if (resizeTimeout) {
            clearTimeout(resizeTimeout);
          }

          resizeTimeout = setTimeout(() => {
            console.log('üîÑ ÂÆπÂô®Â§ßÂ∞èËÆäÂåñÔºåÂà∑Êñ∞Âú∞Âúñ');
            invalidateSize();
          }, 200);
        });

        resizeObserver.observe(mapContainer.value);
        console.log('‚úÖ ResizeObserver Â∑≤Ë®≠ÁΩÆ');
      };

      // üßπ ÁîüÂëΩÈÄ±ÊúüÔºöÁµÑ‰ª∂ÊéõËºâ
      onMounted(() => {
        initMap();
        setupResizeObserver();
      });

      // üßπ ÁîüÂëΩÈÄ±ÊúüÔºöÁµÑ‰ª∂Âç∏Ëºâ
      onUnmounted(() => {
        if (resizeTimeout) {
          clearTimeout(resizeTimeout);
        }

        if (resizeObserver) {
          resizeObserver.disconnect();
        }

        if (svg) {
          svg.remove();
          svg = null;
        }

        projection = null;
        path = null;
        zoom = null;
        g = null;
        ringsGroup = null;
        cityGroup = null;
        tooltipGroup = null;
        if (ringTooltip) {
          ringTooltip.remove();
          ringTooltip = null;
        }
        isMapReady.value = false;
      });

      watch(ringMode, () => {
        if (ringTooltip) {
          ringTooltip.style('opacity', 0);
        }
        refreshProjection();
      });

      // Áõ£ËÅΩÂô®Â∑≤ÁßªÈô§

      // üì§ ËøîÂõûÁµÑ‰ª∂ÂÖ¨ÈñãÁöÑÂ±¨ÊÄßÂíåÊñπÊ≥ï
      return {
        mapContainer,
        mapContainerId,
        navigateToLocation,
        changeRingMode,
        ringMode,
        ringConfigurations,
        planetaryDistanceDisplay,
        planetaryRadiusDisplay,
      };
    },
  };
</script>

<template>
  <!-- üó∫Ô∏è Âú∞Âúñ‰∏ªÂÆπÂô® -->
  <div id="map-container" class="h-100 w-100 position-relative bg-transparent z-0">
    <!-- üó∫Ô∏è D3.js Âú∞ÂúñÂÆπÂô® -->
    <div :id="mapContainerId" ref="mapContainer" class="h-100 w-100"></div>

    <!-- üéõÔ∏è Â∑¶ÂÅ¥ÊéßÂà∂Èù¢Êùø -->
    <div
      class="position-absolute"
      style="top: 50%; left: 0; transform: translateY(-50%); z-index: 1000; padding: 1rem"
    >
      <div class="bg-dark bg-opacity-75 rounded-3 p-3">
        <div class="d-flex flex-column gap-2">
          <button
            type="button"
            class="btn border-0 my-country-btn my-font-sm-white px-4 py-3 text-start"
            :class="[ringMode === 'distance' ? 'active' : '']"
            @click="changeRingMode('distance')"
          >
            Ë°åÊòüË∑ùÈõ¢
          </button>
          <button
            type="button"
            class="btn border-0 my-country-btn my-font-sm-white px-4 py-3 text-start"
            :class="[ringMode === 'radius' ? 'active' : '']"
            @click="changeRingMode('radius')"
          >
            Ë°åÊòüÂçäÂæë
          </button>
        </div>

        <div v-if="ringMode === 'distance'" class="mt-3">
          <p class="my-font-sm-white mb-2">Ë°åÊòüËàáÂ§™ÈôΩÁöÑÂπ≥ÂùáË∑ùÈõ¢ (ÁôæËê¨ÂÖ¨Èáå)</p>
          <ul class="list-unstyled my-font-sm-white mb-0">
            <li
              v-for="item in planetaryDistanceDisplay"
              :key="item.id"
              class="d-flex justify-content-between"
            >
              <span>{{ item.name }}</span>
              <span>{{ item.formatted }}</span>
            </li>
          </ul>
        </div>

        <div v-if="ringMode === 'radius'" class="mt-3">
          <p class="my-font-sm-white mb-2">Ë°åÊòüÂçäÂæë (10ÂÖ¨Èáå)</p>
          <ul class="list-unstyled my-font-sm-white mb-0">
            <li
              v-for="item in planetaryRadiusDisplay"
              :key="item.id"
              class="d-flex justify-content-between"
            >
              <span>{{ item.name }}</span>
              <span
                >{{
                  (Number(item.formatted.replace(/,/g, '')) / 10).toLocaleString('en-US')
                }}
                km</span
              >
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
  @import '../assets/css/common.css';

  #map-container {
    overflow: hidden;
  }

  /* Ë∑ùÈõ¢ÂúìÂúà‰ΩøÁî® D3.js Áπ™Ë£ΩÔºåÂåÖÂê´ÊåáÂÆöÂçäÂæëÂØ¶Á∑öÂúìÂúàËàáÂú∞ÁêÉÈÇäÁïåÂØ¶Á∑öÂúìÂúà */

  :deep(.country) {
    transition: fill 0.2s ease;
  }

  /* ÂúãÂÆ∂Êá∏ÂÅúÊïàÊûúÂ∑≤ÁßªÈô§ */

  :deep(.city-marker) {
    transition: r 0.2s ease;
  }

  :deep(.city-tooltip) {
    transition: opacity 0.2s ease;
  }
</style>
