/**
 * üì¶ Êï∏ÊìöÂ≠òÂÑ≤Ê®°ÁµÑ (Data Store Module)
 *
 * ÁÆ°ÁêÜ D3 Âú∞ÂúñÂØ¶‰æãËàáÂ∞éËà™ÂäüËÉΩ
 * ‰ΩøÁî® Pinia ÁãÄÊÖãÁÆ°ÁêÜÁ≥ªÁµ±Âíå Vue 3 Composition API
 */

// Ê†∏ÂøÉ‰æùË≥¥
import { defineStore } from 'pinia';
import { ref } from 'vue';

/**
 * üè™ Êï∏ÊìöÂ≠òÂÑ≤ÂïÜÂ∫óÂÆöÁæ© (Data Store Definition)
 */
export const useDataStore = defineStore(
  'data',
  () => {
    // Âè∞ÁÅ£‰∏≠ÂøÉÂ∫ßÊ®ôÔºàÁî®ÊñºÂú∞ÂúñÊäïÂΩ±ÂíåÂ∞éËà™Ôºâ
    const taiwanCenter = [120.982025, 23.973875];

    // Âú∞ÂúñÂØ¶‰æã
    const mapInstance = ref(null);
    const setMapInstance = (map) => {
      mapInstance.value = map;
    };

    // Â∞éËà™Âà∞Âè∞ÁÅ£
    const navigateToTaiwan = () => {
      if (!mapInstance.value) {
        console.error('‚ùå Âú∞ÂúñÂØ¶‰æãÊú™Ê∫ñÂÇôÂ∞±Á∑í');
        setTimeout(() => {
          if (mapInstance.value) {
            navigateToTaiwan();
          }
        }, 1000);
        return;
      }

      try {
        if (mapInstance.value.navigateToLocation) {
          mapInstance.value.navigateToLocation(taiwanCenter);
          console.log('üåç ÊàêÂäüÂ∞éËà™Âà∞Âè∞ÁÅ£');
        }
      } catch (error) {
        console.error('‚ùå Âú∞ÂúñÂ∞éËà™Â§±Êïó:', error);
      }
    };

    return {
      taiwanCenter,
      mapInstance,
      setMapInstance,
      navigateToTaiwan,
    };
  },
  {
    persist: true,
  }
);
